import { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faXmarkCircle,
	faBookOpenReader,
	faFilePen,
} from "@fortawesome/free-solid-svg-icons";
import styles from "./tab-controllers.module.scss";
import { useEffect } from "react";
function TabViewer(props) {
	let { tab_array, Viewer, onTabClose, edit_mode, Enable_fn } = props;
	// can luu lai thong tin tab dang hoat dong , khi tai lai se render lai dung noi dung tab do chu ko phai tab cuoi cung trong mang tab_array
	const [CurrentActive, setCurrentActive] = useState(() => {
		let tab = JSON.parse(localStorage.getItem("tab_"));
		if (tab?._id) Viewer(tab?._id);
		return tab || {};
	});
	useEffect(() => {
		localStorage.setItem(
			"tab_",
			JSON.stringify({
				tab: CurrentActive.tab,
				isEdit: edit_mode,
			})
		);
	}, [CurrentActive, edit_mode]);

	// meu mang chua tab thay doi (do them hoac xoa) thi cap nhat lai tab active la tab moi dc them vao
	useEffect(() => {
		let tab = tab_array[tab_array.length - 1];
		Viewer(tab?._id);
		setCurrentActive({ tab, isEdit: edit_mode });
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [tab_array.length]);
	return (
		<div className={styles["wrapper"]}>
			<div className={styles["tabs"]}>
				{tab_array.map((tab) => {
					return (
						<div
							key={tab._id}
							className={`${styles["tab_btn_wraper"]} ${
								CurrentActive.tab?._id === tab._id
									? styles["tabActive"]
									: ""
							}`}
						>
							<button
								className={styles["tab_btn"]}
								onClick={() => {
									setCurrentActive({
										tab,
										isEdit: edit_mode,
									});
									Viewer(tab._id);
								}}
							>
								{tab.name}
							</button>

							<button
								className={styles["tab_btn_delete"]}
								onClick={() => {
									onTabClose(
										tab,
										tab._id === CurrentActive.tab._id
									);
								}}
							>
								<FontAwesomeIcon icon={faXmarkCircle} />
							</button>
						</div>
					);
				})}
			</div>
			<div className={styles["Enable_fn_btns"]}>
				<div className={styles["btn-wrapper"]}>
					<input
						id='Enable_readnable'
						type='checkbox'
						onChange={(e) => {
							Enable_fn.setReadnableContent(
								e.target.checked,
								CurrentActive.tab._id
							);
						}}
					/>
					<label htmlFor='Enable_readnable' title='reader'>
						<FontAwesomeIcon icon={faBookOpenReader} />
					</label>
				</div>
				<div className={styles["btn-wrapper"]}>
					<input
						id='Enable_edit_mode'
						type='checkbox'
						onChange={(e) => {
							Enable_fn.toggleEditMode(
								e.target.checked,
								CurrentActive.tab
							);
						}}
					/>
					<label htmlFor='Enable_edit_mode' title='edit content'>
						<FontAwesomeIcon icon={faFilePen} />
					</label>
				</div>
			</div>
		</div>
	);
}

export default TabViewer;
